.additional__fee {
  padding: 1% 20%;
}
.pt__mob__navbar {
  display: none;
}
.pt__web__navbar {
  display: block;
}
.additionalFee > h2 {
  margin: 30px 0px 10px;
  color: var(--Primary_text-color, #fff);
  font-family: "Poppins", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.additionalFee > button {
  color: #3dc182;
  border: none;
  background: none;
  font-family: "Poppins";
  font-size: 18px;
  padding: 0;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-decoration-line: underline;
}
.additionalFee__Form {
  margin-top: 50px;
}
.additionalFee__Form > p {
  color: #787878;
  font-family: "Poppins";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
.additional__save {
  display: flex;
  justify-content: center;
}
.additional__save > button {
  margin-top: 20px;
  color: #fff;
  font-family: "Poppins", sans-serif;
  border: none;
  padding: 8px 30px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border-radius: 8px;
  background: #00955f;
}
@media only screen and (max-width: 768px) {
  .additional__fee {
    padding: 2px 15px;
  }
  .pt__mob__navbar {
    display: block;
  }
  .pt__web__navbar {
    display: none;
  }
}
